// Colors
$color_dark_blue:   #2d393c;
$color_border: 		#e0e0e0;
$color_pin:			#979797;
$color_gray_dark: 	#484747;
$color_gray_light: 	#838D94;
$color_gray_input: 	#F3F3F3;
$color_gray_page: 	#E5E5E5;
$color_gray_upload: #979797;
$color_gray_upload_alt: #919191;
$color_green: 		#479967;
$color_search_alt:	#DCDCD1;
$color_tan: 		#EDEDE7;

$color_red:         #E75650;

$border:            $color_border;
$bg:                #eee;

// Fonts
$font_montserrat: 	'Montserrat', sans-serif;
$font_lato: 		'Lato', sans-serif;
$font_monospace:    'Menlo', monospace;

// Z indexes
$z_index_modal: 100;

$nav_width: 180px;

$hidpi: 'only screen and (-webkit-min-device-pixel-ratio:1.5),only screen and (min-device-pixel-ratio:1.5)';

// Bootstrap
$brand-primary: 	#3aa3e3;
$brand-darker:      #748885;

$font-family-monospace: $font_monospace;
$border-radius-base: 3px;
$nav-link-hover-bg: $bg;
$nav-tabs-border-color: $color_border;
$nav-tabs-link-hover-border-color: $color_border;
$nav-tabs-active-link-hover-border-color: $color_border;
