@charset "UTF-8";
trix-editor a[data-trix-attachment] {
    color: inherit;
    text-decoration: none; }
trix-editor a[data-trix-attachment]:hover, trix-editor a[data-trix-attachment]:visited:hover {
    color: inherit; }
trix-editor [data-trix-mutable=true] {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none; }
trix-editor [data-trix-mutable=true] img {
    box-shadow: 0 0 0 2px highlight; }
trix-editor .attachment {
    position: relative;
    display: inline-block;
    max-width: 100%;
    margin: 0;
    padding: 0;
    color: #666;
    font-size: 13px; }
trix-editor .attachment .remove {
    display: block;
    position: absolute;
    top: -12px;
    right: -12px;
    width: 24px;
    height: 24px;
    border-radius: 24px;
    line-height: 22px;
    font-size: 0;
    color: black;
    text-align: center;
    text-decoration: none;
    background-color: #fff;
    border: 1px solid #bbb;
    box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.1); }
trix-editor .attachment .remove:after {
    content: '×';
    font-size: 18px;
    font-weight: bold;
    opacity: 0.6; }
trix-editor .attachment .remove:hover:after {
    opacity: 1; }
trix-editor .attachment .caption {
    display: block;
    margin: 4px auto 0 auto;
    padding: 0;
    text-align: center; }
trix-editor .attachment .caption .size:before {
    content: ' · '; }
trix-editor .attachment .caption.caption-editing textarea {
    width: 100%;
    margin: 0;
    padding: 0;
    font-size: 13px;
    line-height: 13px;
    text-align: center;
    border: none;
    outline: none;
    -webkit-appearance: none;
    -moz-appearance: none; }
trix-editor .attachment.attachment-file {
    color: #333;
    line-height: 30px;
    padding: 0 16px;
    border: 1px solid #bbb;
    border-radius: 5px; }
.trix-content blockquote {
    margin: 0 0 0 5px;
    padding: 0 0 0 10px;
    border-left: 5px solid #ccc; }
.trix-content pre {
    font-family: monospace;
    font-size: 12px;
    margin: 0;
    padding: 10px;
    white-space: pre-wrap;
    background-color: #eee; }
.trix-content ul, .trix-content ol, .trix-content li {
    margin: 0;
    padding: 0; }
.trix-content ul li, .trix-content ol li, .trix-content li li {
    margin-left: 20px; }

// The Grid
.grid-fieldtype trix-toolbar {
    margin: 0;
}
